<style>
@import './administratorlist.css';
</style>
<template>
	<div>
<div class="contain" style="z-index: -1;">    
		<div class="item-title">
		<div class="miaoshu">
		<h3></h3>
		<h5></h5>
		</div>
		</div>
		<div class="functions">
			<div class="w_add">			 
			 	 <Button type="success" size="small" @click="addmanager"></Button>		            
			</div>
			<div>
			<Table border :columns="columns7" :data="data6" ref="selection"></Table>	
			</div>					
		<!-- -->
	    <Modal v-model="modalx" title="" @on-ok="modelok" @on-cancel="modelcancel">
        <RadioGroup v-model="animal" @on-change="redianpu">
        <Radio label="2"><span></span></Radio>
        <Radio label="1"><span></span></Radio>
        </RadioGroup>
        </Modal>
      <!---->
      <!-- -->
	    <Modal v-model="modaladd" title="" @on-ok="addok" @on-cancel="addcancel">
	    	<div style="text-align: center;">
	    		<img :src="imgurl"/>
	    	</div>       
        </Modal>
      <!---->
      
		</div>
</div>
</div>
</template>
<script>
var deleteid;
var repairid;
var redianpuvalue;
var redianpuname;
 export default {
        data () {
            return {
            	animal:'',
            	formItem: {
                    input: '',
                    select: '',
                    radio: 'male',
                    checkbox: [],
                    switch: true,
                    date: '',
                    time: '',
                    slider: [20, 50],
                    textarea: ''
               },
                animal: '',
            	modalx:false,
            	modaladd:false,
            	imgurl:'',
                columns7: [
                    {
                    	title:'ID',
                    	key:'id'
                    },
                    {
                        title: '',
                        key: 'username'
                    },
                    {
                        title: '',
                        key: 'role'
                    },
                    {
                        title: '',
                        key: 'birthday'
                    },
                    {
                    	title:'',
                    	key:'sex'
                    },
                    { 
                    	title:'',
                    	key:'name'
                    },              
                    {
                        title: '',
                        key: 'lastLogin'
                    }, 
                    {
                    	title:'',
                    	key:'created'
                    },
                    {
                        title: '',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                        	if(this.data6[params.index].role==""){
                                        	this.modalx=false;	
                                        	}else{
                                        	this.modalx=true;
                                            this.repair(params.index);	
                                        	}
                                        }
                                    }
                                }, ''),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index);
                                            this.deletemanager();
                                        }
                                    }
                                }, '')
                            ]);
                        }
                    }
                ],         
                data6: [],                            
                model1:'',
                model2:'',
                model3:'',
                model4:'',             
                value:''
            }
        },
        methods: {
            remove (index) {
                this.data6.splice(index, 1);
                deleteid= this.data6[index].id;            
            },
            //id
           repair(index){
             	repairid= this.data6[index].id; 
             },
            //  
             requestmanager(){        
              	var $that = this;
				this.axios({
					method: 'get',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url:'/api/clerks'
				}).then(function(res) {
				    $that.data6=res.data.data;
				    for (var i=0;i<res.data.data.length;i++) {
				    	 if (res.data.data[i].sex==1) {
				    	 	res.data.data[i].sex= ''
				    	 } else{
				    	 	res.data.data[i].sex= ''
				    	 }
				    }
		            for (var i=0;i<res.data.data.length;i++) {
		           	 if (res.data.data[i].role==0) {
		           	  	res.data.data[i].role=''
		           	  } else if(res.data.data[i].role==1){
		           	  	res.data.data[i].role=''
		           	  }else if(res.data.data[i].role==2){
		           	  	res.data.data[i].role=''
		           	  }else{
		           	  	res.data.data[i].role=''
		           	  }
		            }
				})
             },
            //
           addmanager(){
           this.modaladd=true;
              	var that = this;
				this.axios({
					method: 'post',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url:'/api/users/0/qrcodes/addclerk'
				}).then(function(res) {
					that.imgurl= 'https://mp.weixin.qq.com/cgi-bin/showqrcode?ticket='+res.data.data.ticket			
				})
            },
             // 
              deletemanager(){
              	var $that = this;
				this.axios({
					method: 'delete',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url:'/api/clerks/'+deleteid
				}).then(function(res) {
					console.log(res)			
				})
             },
            redianpu(value){
             	redianpuvalue=value;
             	if (value==1) {
             		redianpuname=''
             	} else{
             		redianpuname=''
             	}
             },
            modelok () {
            	var that = this;
				this.axios({
					method: 'put',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url:'/api/clerks/'+repairid+'/roles/'+redianpuvalue
				}).then(function(res) {
					if (res.data.data.role==1) {
				   		res.data.data.role='';
				   	} else{
				   		res.data.data.role='';
				   	}
					var goodsType=_.find(that.data6, function(o) {
					return o.id===res.data.data.id; 
				     });
				   if(goodsType){			   	
					goodsType.role=res.data.data.role
				   }
				   var selected=_.find(that.data6, function(o) {
					return o.id===repairid; 
				     });
				   if(selected){			   	
					selected.role=redianpuname
				   }
				   console.log(goodsType)
				})
                this.$Message.info('Clicked ok');
            },
            //
            
            //
            
            
            modelcancel () {
                this.$Message.info('Clicked cancel');
            },
            addok(){
            	this.$Message.info('Clicked ok');
            },
            addcancel(){
            	this.$Message.info('');
            }
        },
        mounted() {
			this.requestmanager();
		}
    }
</script>




