<template>
	<div>
<div class="contain" style="z-index: -1;">    
		<div class="item-title">
		<div class="miaoshu">
		<h3></h3>
		<h5></h5>
		</div>
		</div>
		<div class="functions">
			<div class="search">
			   <Select v-model="ypeople" size="small" style="width:150px;">
               <Option v-for="item in allsort" :value="item.value" :key="item.value">{{ item.label }}</Option>
              </Select>           
               <Select v-model="ystaue" size="small" style="width:100px">
               <Option v-for="item in allone" :value="item.value" :key="item.value">{{ item.label }}</Option>
               </Select>
               <DatePicker type="daterange" placement="bottom-end" placeholder="" style="width: 200px" size="small" v-model="timerange"></DatePicker>
               <Input v-model="value" placeholder="..." style="width: 150px" size="small"></Input>
               <Button type="info" size="small" @click="queryinformations"></Button>
               <Poptip confirm title="" @on-ok="ok" @on-cancel="cancel" placement="right">
               <Button  size="small" type="warning"></Button>           
               </Poptip> 
               <Button @click="handleSelectAll('{{one}}')" size="small" type="error">{{statu}}</Button>             
			</div>			
			<Table border :columns="columns7" :data="data6" ref="selection" @on-selection-change="selected" height="500"></Table>
			<div class="paging">
			<Page :total="totalpage" :page-size="pageSize"  show-total  @on-change="changepage" :transfer='ifs'></Page>
			</div>
	<!---->
	<Modal v-model="modalx" title="" @on-ok="modelok" @on-cancel="modelcancel">
       <Form :model="formItem" :label-width="80">
       	<FormItem label="">
            <Input v-model="yname" placeholder="..."></Input>
        </FormItem>
        <FormItem label="">
            <Input v-model="ytel" placeholder="..."></Input>
        </FormItem>
        <FormItem label="">
            <Input v-model="yarea" placeholder="..."></Input>
        </FormItem>
        <FormItem label="">
            <Select v-model="statusnum">
                <Option value="0"></Option>
                <Option value="1"></Option>
                <Option value="2"></Option>
                <Option value="3"></Option>
            </Select>
        </FormItem>
       </Form>
      </Modal>
      <!---->
		</div>
</div>
</div>
</template>
<script>
var arrsid;
var deleteid='';
var arrids;
var ids;
var repairid;
 export default {
        data () {
            return {
            	yname:'',
            	ytel:'',
            	yarea:'',
            	totalpage:0,
            	pageSize:10,
            	ifs:false,
            	ajaxHistoryData:[],
            	formItem: {
                    input: '',
                    select: '',
                    radio: 'male',
                    checkbox: [],
                    switch: true,
                    date: '',
                    time: '',
                    slider: [20, 50],
                    textarea: ''
               },
                columns7: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },              
                    {
                     title: 'id',
                        key: 'userId',
                        align: 'center'	
                    },
                    {
                        title: '',
                        key: 'name',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'phone',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'area',
                        align: 'center'
                    },
                     {
                        title: '',
                        key: 'created',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'status',
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.modalx = true;
                                            this.repair(params.index);
                                        }
                                    }
                                }, ''),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index);
                                            this.radiodeleteinformations();
                                        }
                                    }
                                }, '')
                            ]);
                        }
                    }
                ],
                data6: [],
                allsort:[
                    {
                        value: 'allsort',
                        label: ' '
                    },
                    {
                        value: 'twosui',
                        label: ''
                    },
                ],
                allone:[
                    {
                        value: '0',
                        label: ''
                    },
                    {
                        value: '1',
                        label: ''
                    },
                    {
                        value: '3',
                        label: ''
                    },
                    {
                        value: '4',
                        label: ''
                    }
                ],
                yes:'',
                no:'',
                ypeople:'',
                model2:'',
                ystaue:'',
                model4:'',
                statu:'',
                value:'',
                modalx: false,
                options2:'',
                arrs:[],
                statusnum:'',
                timerange:''
            }
        },
        methods: {
        	//
           remove(index){
            	deleteid=this.data6[index].id
                this.data6.splice(index, 1);
            },
           repair(index){
            	repairid=this.data6[index].id
            	console.log(repairid)
            },
             //
            radiodeleteinformations(){
         	 var a=[];
           	 a.push(deleteid)
         	 this.axios({
             method: 'delete',
             url: '/api/reservations/'+a,
         })     
            },
            //
            handleSelectAll (status) {
            	if (this.one==false) {
            		this.$refs.selection.selectAll(false);
            		this.statu=this.yes;
            		this.one=true
            	} else{
            		 this.$refs.selection.selectAll(true);
            		 this.statu=this.no;
            		 this.one=false;                 		 
            	}
               
            },       
            //index
            selected(selection){
            	arrsid={};
            	arrids=[];
	         	for(var i=0;i<=selection.length-1;i++){ 
	         		arrsid[selection[i].id]=true;
	         		arrids.push(selection[i].id)
	          }       
            },
            // 
            ok(){          	
                var evens = _.filter(this.data6, function(n) {          
                	return !arrsid[n.id];                
                }); 
                this.data6=evens;           
         	    this.axios({
                method: 'delete',
                url: '/api/delreservations/'+arrids,
              })
                this.$Message.info('');                                     
            },
            cancel () {
                this.$Message.info('');
            },
            modelok () {
          	    this.putinformations();
                this.$Message.info('Clicked ok');
            },
            modelcancel () {
                this.$Message.info('Clicked cancel');
            },
            //
            requestinformations() {
				var that = this;
				this.axios({
					method: 'get',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url: '/api/reservations'
				}).then(function(res) {
					that.data6=res.data.data;
					//      
					that.ajaxHistoryData=res.data.data;
                    that.totalpage = res.data.data.length;
                // 
                if(res.data.data.length < that.pageSize){
                    that.data6 = that.ajaxHistoryData;
                }else{
                    that.data6 = that.ajaxHistoryData.slice(0,that.pageSize);
                }
					console.log(res.data.data)
					for(var i=0;i<res.data.data.length;i++){
						 if (res.data.data[i].status==0) {
						 	res.data.data[i].status="";
						 } else if(res.data.data[i].status==1){
						 	res.data.data[i].status="";
						 }else if(res.data.data[i].status==2){
						 	res.data.data[i].status="";
						 }else{
						 	res.data.data[i].status="";
						 }
					}									
					repairid=res.data.data.id
				})
			},
            // 
            putinformations() {
				var that=this
				this.axios({
				  method: 'put',
				  url: '/api/reservations/'+repairid,
                   data:{
                   name:that.yname,
                   phone:that.ytel,
                   area:that.yarea
                 },
				}).then(function(res) {
					console.log(res)
				}).catch(function(res){
					console.log(res)
				})
			},
			//
          queryinformations() {
           console.log(this.timerange)
		    var that=this
		    var names;
		    var phones;
            console.log(this.ypeople)
            if(this.ypeople=='allsort'){
            	 names=this.value
            }else if(this.ypeople=='twosui'){
            	 phones=this.value
            }else{
            	names=null;
            	phones=null;
            }
				this.axios({
					method: 'post',
				    url: '/api/filterreservation',
                    data:{
                    name:names,
                    phone:phones,
                    status:that.ystaue,
                    time1:that.timerange[0],
                    time2:that.timerange[1]
                 },
				}).then(function(res) {				
					that.data6=res.data.data.reservation
				})
			},
			// 
            changepage(index){
                var _start = ( index - 1 ) * this.pageSize;
                var _end = index * this.pageSize;
                this.data6= this.ajaxHistoryData.slice(_start,_end);
            }
        },
        mounted() {		
			this.requestinformations();
		}
    }
</script>




