<style>
.contain{
margin-left: 210px;
margin-right: 10px;
}
h3{
	font-size: 16px;
    font-weight: normal;
    line-height: 20px;
    color: #333;
    display: inline-block;  
}
h5{
	font-size: 12px;
    font-weight: normal;
    line-height: 18px;
    color: #777;
    display: inline-block;
    margin-left: 10px;
}
.item-title{
	height: 55px;
	line-height: 25px;
    white-space: nowrap;
    width: 98%;
    padding-top: 3px;
    margin: 20px 1%;
    border-bottom: solid 1px #E6E6E6;
}
.search{
	display: inline-block;
	margin-top: 10px;
	margin-bottom: 30px;
	display: inline-block;
}
.listtittle{
	 font-size: 12px;
    font-weight: normal;
    line-height: 24px;
    color: #777;
    vertical-align: bottom;
    letter-spacing: normal;
    display: inline-block;
    padding: 0
}
.tips{
	color: #0ba4da;
}
.routerx{
	color: #fff;
}
.paging{
	margin-top: 50px;
	float: right;
	margin-right: 20px;
}
.center{
        width: 300px;
        margin: 10px auto;
        overflow: hidden;
    }
.center-right{
        float: right;
    }
.ivu-message{
    	z-index: 9999999999;
    }
</style>
<template>
	<div>
<div class="contain" style="z-index: -1;">
	    <div>
		<div class="item-title">
		<div class="miaoshu">
		<h3></h3>
		<h5></h5>
		</div>
		</div>
		<div class="functions">
			<div class="search">
			   <Select v-model="model1" size="small" style="width:150px;"placeholder="">
               <Option v-for="item in allsort" :value="item.value" :key="item.value">{{ item.label }}</Option>
               </Select>
               <Select v-model="model2" size="small" style="width:100px" placeholder="">
               <Option v-for="item in allbrand" :value="item.value" :key="item.value">{{ item.label }}</Option>
               </Select>
               <Select v-model="model3" size="small" style="width:100px"placeholder="">
               <Option v-for="item in allone" :value="item.value" :key="item.value" >{{ item.label }}</Option>
               </Select>
               <Select v-model="model4" size="small" style="width:100px" placeholder="">
               <Option v-for="item in alltwo" :value="item.value" :key="item.value" >{{ item.label }}</Option>
               </Select>
               <Input v-model="value" placeholder="..." style="width: 150px" size="small"></Input>
               <Button type="info" size="small"></Button>
               <Button type="success" size="small" @click="modaladd=true"></Button>          
               <Poptip confirm title="" @on-ok="ok" @on-cancel="cancel" placement="right">
               <Button  size="small" type="warning"></Button>           
               </Poptip> 
               <Button  size="small" type="primary"  @click="exportData(1)"></Button>
               <Button @click="handleSelectAll('{{one}}')" size="small" type="error">{{statu}}</Button>
			</div>			
			<Table border :columns="columns7" :data="data6" ref="table" @on-selection-change="selected" height="500"></Table>
	<!-- -->
       <Modal v-model="modalx" title="" @on-ok="modelok" @on-cancel="modelcancel">
	  <Tabs value="name1">
        <TabPane label=" " name="name1"><Form :model="formItem" :label-width="80">
       	<FormItem label="">
            <Input v-model="formItem.input" placeholder="..."></Input>
        </FormItem>
        	<FormItem label="">
            <Input v-model="formItem.input" placeholder="..."></Input>
        </FormItem>    
        	<FormItem label="">
            <Input v-model="formItem.input" placeholder="..."></Input>
        </FormItem>
        	<FormItem label="">
            <Select v-model="model1" style="width:100px" size="small">
                 <Option v-for="item in cityList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
				 <Select v-model="model1" style="width:100px" size="small">
                 <Option v-for="item in cityList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
				 <Select v-model="model1" style="width:100px" size="small">
                 <Option v-for="item in cityList" :value="item.value" :key="item.value" >{{ item.label }}</Option>
				 </Select>
        </FormItem>
        	<FormItem label="">
            <Input v-model="formItem.input" placeholder="..."></Input>
        </FormItem>
        	<FormItem label="">
            <Input v-model="formItem.input" placeholder="..."></Input>
        </FormItem>
        	<FormItem label="">
            <Input v-model="formItem.input" placeholder="..."></Input>
        </FormItem>
        	<FormItem label="">
            <Input v-model="formItem.input" placeholder="..."></Input>
        </FormItem>
        <FormItem label="">
            <Input v-model="formItem.input" placeholder="..."></Input>
        </FormItem>
        <FormItem label="">
            <Input v-model="formItem.input" placeholder="..."></Input>
        </FormItem>
        <FormItem label="">
            <Input v-model="formItem.input" placeholder="..."></Input>
        </FormItem>
        <FormItem label="">
        	 <Upload action="//jsonplaceholder.typicode.com/posts/">
             <Button type="ghost" icon="ios-cloud-upload-outline"></Button>
    </Upload>
        </FormItem>
        </Form>
        </TabPane>
        <TabPane label="" name="name2"></TabPane>
        <TabPane label="" name="name3"></TabPane>
    </Tabs>    
        </Modal>
      <!---->
      <!-- -->
    <Modal v-model="modaladd" title="" @on-ok="modelok" @on-cancel="modelcancel">
    	<div id="" style="height:300px;">
    	<Form :model="formItem" :label-width="80">
       	<FormItem label="">
            <Input v-model="productname" placeholder="..."></Input>
        </FormItem>
        	<FormItem label="">
            <Cascader :data="sortdata" v-model="sortvalue" style="width: 200px;"  @on-change="selectdata"></Cascader>		 
        </FormItem>
        </Form>
    	</div>   	  
        </Modal>
      <!---->
			<div class="paging">
			<Page :total="100"></Page>
			</div>
		</div>
</div>
</div>
</div>
</template>
<script>
import Vue from 'vue';
let _ = require("lodash");
var selectname;
var selectid;
import tableparts from '../Smallparts/tableparts'
 export default {
 	  components: {
     tableparts:tableparts
              },
  
        data () {
            return {
            	sortvalue: [],
            	sortdata: [{
                    value: 'beijing',
                    label: '',
                    children: [
                        {
                            value: 'gugong',
                            label: ''
                        }                    
                    ]
                },
                        {
                    value: 'jiangsu',
                    label: '',
                    children: [
                        {
                            value: 'nanjing',
                            label: ''                   
                        },
                        {
                            value: 'suzhou',
                            label: ''                          
                        }
                    ],
                }],
            	formItem: {
                    input: ''
               },
                animal: '',
            	modalx: false,
                columns7: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },
                    {
                        title: '',
                        key: 'name',
                         align: 'center'
                    },
                    {
                        title: '',
                        key: 'sort',
                         align: 'center'
                    },
                    {
                        title: ' ',
                        key: 'originalprice',
                         align: 'center'
                    },
                    {
                        title: '',
                        key: 'price',
                         align: 'center'
                    },
                     {
                        title: '',
                         key: 'uptime',
                         align: 'center',
                         width: 110,
                    },              
                     {
                        title: '',
                        key: 'created',
                         align: 'center',
                         width: 110,
                    },
                     {
                        title: '',
                        key: 'logo',
                         align: 'center'
                    },
                    {
                        title: '/',
                        key: 'updown',
                        align: 'center',
                        render: (h) => {
                        return h(tableparts,{
                        	props:{
                        		yes: '',
                                 no: ''
                        	}
                        });
                      }
                    },
                    {
                        title: '',
                        key: 'path',
                        align: 'center',
                        render:(h,params)=>{
                        	return h('img',{
                        		domProps:{
                                src:params.row.path},
                                style: {
                                        width: '100px',
                                        height:'50px'
                                       }                       		
                        	})
                        }
                    },
                    {
                        title: '',
                        key: 'action',
                        width: 200,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                            h('Button', {
                                    props: {
                                        type: 'success',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, ''),
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.modalx=true
                                        }
                                    }
                                }, ''),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, '')
                            ]);
                        }
                    }
                ],
                data6: [],
                allsort:[
                    {
                        value: 'allsort',
                        label: ''
                    },
                    {
                        value: 'twosui',
                        label: '0-2'
                    },
                ],
                allbrand:[
                    {
                        value: 'allbrand',
                        label: ''
                    }
                ],
                allone:[
                    {
                        value: 'allsort',
                        label: ''
                    },
                    {
                        value: 'twosui',
                        label: '0-2'
                    },
                ],
                alltwo:[
                    {
                        value: 'allsort',
                        label: ''
                    },
                    {
                        value: 'twosui',
                        label: '0-2'
                    },
                ],
                yes:'',
                no:'',
                model1:'',
                model2:'',
                model3:'',
                model4:'',
                statu:'',
                value:'',
                arrs:[],
                modaladd:false,
                cityList:[],
                productname:''
            }
        },
        methods: {
            remove (index) {
                this.data6.splice(index, 1);
            },
            //
            handleSelectAll (status) {
            	if (this.one==false) {
            		this.$refs.table.selectAll(false);
            		this.statu=this.yes;
            		this.one=true
            	} else{
            		 this.$refs.table.selectAll(true);
            		 this.statu=this.no;
            		 this.one=false;                 		 
            	}
               
            },
            //
            requestgoods(){
				var $that = this;
				this.axios({
					method: 'get',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url: '/api/goods'
				}).then(function(res) {
					$that.data6=res.data.data
				})
			},
			//
			requestgoodsort(){
				var $that = this;
				this.axios({
					method: 'get',
					headers: {
						'X-Requested-With': 'XMLHttpRequest',
						'ContentType': 'application/json;charset=UTF-8'
					},
					url: '/api/goodstypes/type'
				}).then(function(res) {
					var tempData ={}					
					var datas = res.data.data;
					var m = 0;
					var len = datas.length;
					var tmp;
					var children;
					var idx=0;
					var finalData;
					var childrenArr;
					if(len==0){
						return;
					}
					var finalDatas=[];
					var parentArr=_.filter(datas,function(data){
						return !data.parentId && !data.disabled;
					});
					for(m;m<parentArr.length;m++){
						tmp = parentArr[m];
						children=[];						
						finalData ={
							value:tmp.id,
							label:tmp.name,
							children:children
						};
						finalDatas[m]=finalData;
						tempData[tmp.id]=children;
					}
					
					var childrenArr = _.filter(datas,function(data){
						return !!data.parentId && !data.disabled;
					});
					len=childrenArr.length;
					for(m=0;m<len;m++){
						tmp = childrenArr[m];
						finalData ={
							value:tmp.id,
							label:tmp.name
						};
						children = tempData[tmp.parentId];
						if(!children){
							continue;
						}
						children[children.length]=finalData;
					}
					$that.sortdata=finalDatas;
					console.log($that.sortdata)
				})
			},
			//
			selectdata(value,selectedData){
            selectid=selectedData[value.length-1].value;
            selectname=selectedData[value.length-1].label;
			},
			//
			addgoods(){
			var that=this;
			this.axios({
				method:'post',
				headers:{
				'X-Requested-With': 'XMLHttpRequest',
				'ContentType': 'application/json;charset=UTF-8'
				},
				url:'/api/goods',
				data:{
					name:that.formItem.input,
					goodsTypeId:selectid,
					disabled:true
				}
			}).then(function(res){
				console.log(res)
			})
			},

            //index
            selected(selection){
            	arrsid={};
	         	for(var i=0;i<=selection.length-1;i++){ 
	         		arrsid[selection[i].id]=true;
	          	}       
            	console.log(arrsid);
            },
            //
            ok(){          	
                var evens = _.filter(this.data6, function(n) {          
                	return !arrsid[n.id];                
                }); 
                this.data6=evens;
                console.log(this.data6)
                this.$Message.info('');                                     
            },
            cancel () {
                this.$Message.info('');
            },
            //
            modelok () {
            	this.addgoods();
                this.$Message.info('');
                setTimeout(() => {
                this.$router.push({ name: 'addgoods',query: {name:selectid}, params: {name:selectid}})
				}, 2000)
            },
            modelcancel () {
                this.$Message.info('Clicked cancel');
            },
            //
              exportData (type) {
                if (type === 1) {
                    this.$refs.table.exportCsv({
                        filename: 'The original data'
                    });
                } else if (type === 2) {
                    this.$refs.table.exportCsv({
                        filename: 'Sorting and filtering data',
                        original: false
                    });
                } else if (type === 3) {
                    this.$refs.table.exportCsv({
                        filename: 'Custom data',
                        columns: this.columns8.filter((col, index) => index < 4),
                        data: this.data7.filter((data, index) => index < 4)
                    });
                }
            } 
        },
        mounted() {		
			this.requestgoods();
			this.requestgoodsort();
		}
    }
</script>




