<style>
.contain{
margin-left: 210px;
margin-right: 10px;
}
h3{
	font-size: 16px;
    font-weight: normal;
    line-height: 20px;
    color: #333;
    display: inline-block;
}
h5{
	font-size: 12px;
    font-weight: normal;
    line-height: 18px;
    color: #777;
    display: inline-block;
    margin-left: 10px;
}
.item-title{
	height: 35px;
	line-height: 25px;
    white-space: nowrap;
    width: 98%;
    padding-top: 3px;
    margin: 20px 1%;
    border-bottom: solid 1px #E6E6E6;
}
.search{
	display: inline-block;
	margin-top: 10px;
	margin-bottom: 30px;
	display: inline-block;
}
.listtittle{
	 font-size: 12px;
    font-weight: normal;
    line-height: 24px;
    color: #777;
    vertical-align: bottom;
    letter-spacing: normal;
    display: inline-block;
    padding: 0
}
.tips{
	color: #0ba4da;
}
.routerx{
	color: #fff;
}
</style>
<template>
	<div>
<div class="contain" style="z-index: -1;">    
		<div class="item-title">
		<div class="miaoshu">
		<h3> - </h3>
		<h5></h5>
		</div>
		</div>
		<Alert>
       <span class="tips"><Icon type="ios-checkmark-outline" size="16" color="red"></Icon>&nbsp;&nbsp;</span>
  <template slot="desc"></template>
    </Alert>
		<div class="functions">
			<div class="search">
				
			   <Input v-model="value" placeholder="..." style="width: 150px" size="small"></Input>
               <Input v-model="value" placeholder="emil..." style="width: 150px" size="small"></Input>
               <DatePicker type="daterange"  placement="bottom-end" placeholder="" style="width: 200px"size="small"></DatePicker>
               <Button type="info" size="small"></Button>                     
			</div>			
			<Table border :columns="columns7" :data="data6" ref="selection"></Table>
			<div class="paging">
			<Page :total="100"></Page>
			</div>
		</div>
</div>
</div>
</template>
<script>
 export default {
        data () {
            return {
            	options2:'',
                columns7: [
                    {
                        type: 'selection',
                        width: 60,
                        align: 'center'
                    },
                    {
                        title: 'ID',
                        key: 'id',
                    },
                    {
                        title: '',
                        key: 'name'
                    },
                    {
                        title: '',
                        key: 'numbers'
                    },
                    {
                        title: '',
                        key: 'sort'
                    },
                    {
                        title: '',
                        key: 'price'
                    },             
                    {
                        title: '',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, ''),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, '')
                            ]);
                        }
                    }
                ],
                data6: [
                    {   id:151,
                        name: '',
                        numbers: 18,
                        sort: '',
                        price:1000,
                        interduce:'',
                        updown:'',
                        news:'yes',
                        kucun:'1000'                      
                    }             
                ],
                allsort:[
                    {
                        value: 'allsort',
                        label: ''
                    },
                    {
                        value: 'twosui',
                        label: '0-2'
                    },
                ],
                allbrand:[
                    {
                        value: 'allbrand',
                        label: ''
                    }
                ],
                allone:[
                    {
                        value: 'allsort',
                        label: ''
                    },
                    {
                        value: 'twosui',
                        label: '0-2'
                    },
                ],
                alltwo:[
                    {
                        value: 'allsort',
                        label: ''
                    },
                    {
                        value: 'twosui',
                        label: '0-2'
                    },
                ],
                yes:'',
                no:'',
                model1:'',
                model2:'',
                model3:'',
                model4:'',
                statu:'',
                value:''
            }
        },
        methods: {
            show (index) {
                this.$Modal.info({
                    title: 'User Info',
                    content: `Name${this.data6[index].name}<br>Age${this.data6[index].age}<br>Address${this.data6[index].address}`
                })
            },
            remove (index) {
                this.data6.splice(index, 1);
            },
            alerts(){
            	alert(123)
            }
        }
    }
</script>




